import React, { useState, useEffect } from "react";
import "./App.css";
import icon from "./icon.png";

function App() {
  const [searchInput, setSearch] = useState("");
  const [sortZA, setSort] = useState(false);
  const [input, setInput] = useState("");
  const [modInput, setMod] = useState("");
  const [vocList, setList] = useState(
    JSON.parse(localStorage.getItem("words")) || []
  );

  useEffect(() => {
    localStorage.setItem("words", JSON.stringify(vocList));
  }, [vocList]);

  function addWord(e) {
    e.preventDefault();
    input.split(", ").forEach(word => {
      if (vocList.indexOf(word) >= 0) {
        return alert(`"${word}" is already in the vocabulary list`);
      } else {
        setList(prevList =>
          [...prevList, word].sort((a, b) =>
            a.localeCompare(b, "en", { sensitivity: "base" })
          )
        );
      }
      setInput("");
    });
  }

  function download(filename, text) {
    var element = document.createElement("a");
    element.setAttribute(
      "href",
      "data:text/plain;charset=utf-8," + encodeURIComponent(text)
    );
    element.setAttribute("download", filename);

    element.style.display = "none";
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
  }

  // Start file download.
  function handleDownload() {
    // Generate download of hello.txt file with some content
    var text = String(vocList);
    var filename = "vocabList.txt";

    download(filename, text);
  }

  function deleteWord(index) {
    setList(prevList => prevList.filter((_, i) => i != index));
  }

  function modifyWord(e, index) {
    e.preventDefault();
    setMod("");
    if (vocList.indexOf(modInput) >= 0) {
      return alert(`"${modInput}" is already in the vocabulary list`);
    } else {
      deleteWord(index);
      setList(prevList =>
        [...prevList, modInput].sort((a, b) =>
          a.localeCompare(b, "en", { sensitivity: "base" })
        )
      );
      toggle(index);
    }
  }

  function toggle(index) {
    let display = document.getElementById("modify" + String(index)).style
      .display;
    if (display == "none") {
      document.getElementById("modify" + String(index)).style.display = "block";
    } else if (display == "block") {
      document.getElementById("modify" + String(index)).style.display = "none";
    }
  }

  function toggleInput() {
    document.querySelector(".search-input").classList.toggle("active");
  }

  function handleMouseEnter() {
    document.getElementById("1").classList.add("hover");
  }
  function handleMouseLeave() {
    document.getElementById("1").classList.remove("hover");
  }
  function sortA() {
    setSort(false);
  }
  function sortZ() {
    setSort(true);
  }

  return (
    <div className="App">
      <header className="app-header">
        <h1>Aurore English Vocabulary App</h1>
      </header>
      <div id="1" className="dwn-text">
        Download Current List
      </div>
      <button
        className="dwn-btn"
        onClick={handleDownload}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
      >
        <img src={icon} width="20px" />
      </button>
      <form onSubmit={addWord} className="add-form">
        <input
          className="add-btn"
          type="text"
          value={input}
          onChange={e => setInput(e.target.value)}
          placeholder="Type word or list of words here..."
          required
        />
        <button>Add Word</button>
      </form>
      <div className="sort-container">
        Sort:{" "}
        <a className="link" href="#" onClick={sortA}>
          A-Z
        </a>{" "}
        |{" "}
        <a className="link" href="#" onClick={sortZ}>
          Z-A
        </a>{" "}
        |{" "}
        <a className="link" href="#" onClick={toggleInput}>
          Search
        </a>
        <input
          className="search-input"
          type="text"
          value={searchInput}
          onChange={e => setSearch(e.target.value)}
        ></input>
      </div>
      <div className="list">
        {vocList.length ? (
          vocList
            .sort((a, b) => {
              if (!sortZA) {
                return a.localeCompare(b, "en", { sensitivity: "base" });
              } else {
                return a.localeCompare(b, "en", { sensitivity: "base" }) == 1
                  ? -1
                  : a.localeCompare(b, "en", { sensitivity: "base" }) == -1
                  ? 1
                  : 0;
              }
            })
            .filter(word =>
              searchInput.length
                ? word.toLowerCase().includes(searchInput.toLowerCase())
                : true
            )
            .map((word, i) => {
              return (
                <>
                  <div key={i} className="list-item">
                    <h2>- {word}</h2>
                    <button
                      onClick={() => toggle(i)}
                      className="toggle-mod-btn"
                    >
                      Modify
                    </button>

                    <button onClick={() => deleteWord(i)} className="del-btn">
                      Delete
                    </button>
                  </div>
                  <div>
                    <form
                      onSubmit={e => modifyWord(e, i)}
                      style={{ display: "none" }}
                      id={"modify" + String(i)}
                    >
                      <input
                        className="toggle-mod-input"
                        type="text"
                        value={modInput}
                        onChange={e => setMod(e.target.value)}
                        placeholder="Modify vocab"
                      />
                      <button className="mod-btn">Submit</button>
                    </form>
                  </div>
                </>
              );
            })
        ) : (
          <div className="novocy">No Vocabulary Yet</div>
        )}
      </div>
    </div>
  );
}

export default App;
